.clubs {

  &__title-box {
    margin: 0 auto;
    left: 0;
    right: 0;
    text-align: center;
    z-index: 9;
    position: absolute;
    color: #fff;
    border-top: 80px solid rgba(253, 116, 0, .5);
    border-left: 50px solid transparent;
    border-right: 50px solid transparent;
    height: 0;
    width: 470px;
  }

  &__title {
    position: absolute;
    top: -65px;
    left: 80px;
    margin: 0;
    font-family: 'Roboto Slab', serif;
    font-size: 36px;
    font-weight: 400;
    line-height: normal;
    color: #fff;
  }

}

.clubs-carousel {
  position: relative;

  &__item {
    //background: #0000AA;
  }

  &__nav-container {
    position: absolute;
    z-index: 1;
    bottom: 20px;
    left: 0;
    right: 0;
    text-align: center;
  }

  &__nav-prev {
    display: inline-block;
    margin: 0 10px;
    width: 32px;
    height: 32px;
    border-radius: 100%;
    background: url(../images/car-prev-or.png) no-repeat;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(.25,.8,.25,1);

    &:hover {
      box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    }

    &:active {
      opacity: .8;
    }

  }

  &__nav-next {
    display: inline-block;
    margin: 0 10px;
    width: 32px;
    height: 32px;
    border-radius: 100%;
    background: url(../images/car-next-or.png) no-repeat;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(.25,.8,.25,1);

    &:hover {
      box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    }

    &:active {
      opacity: .8;
    }

  }

}

