/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .clubs {

    &__title-box {
      display: block;
      height: 100%;
      padding: 0;
      margin: 0 auto;
      left: 0;
      right: 0;
      position: relative;
      color: transparent;
      border: none;
      width: auto;
      background: #0f0f0f;

      &:after {
        clear: both;
        content: "";
        display: table;
      }

    }

    &__title {
      padding: 30px 20px;
      position: relative;
      text-align: center;
      left: 0;
      top: 0;
      font-size: 26px;
    }

  }
}

