.progress {
  position: relative;
  background: url(../images/bg_roma.jpg) no-repeat;
  background-position: center;
  background-size: 100%;
  z-index: -2;

  &__shad {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, .63);
    z-index: -1;
  }

  &__item-list {
    display: flex;
    flex-flow: row wrap;
    padding: 0 0 45px 45px;

    //&:after {
    //  clear: both;
    //  content: "";
    //  display: table;
    //}

  }

  &__item {
    &-col {
      &-4-12 {
        //float: left;
        width: 33.33%;
        height: 100%;
        padding: 0 45px 45px 0;
      }
    }
  }

  &__item {
    //width: 33.33%;
    //margin: 0 45px 45px 0;
    border-radius: 1px;
    border: 1px solid rgba(255, 255, 255, .37);
    background: rgba(236, 240, 244, .15);

    & p {
      padding: 15px 30px;
      margin: 0;
      font-family: 'Open Sans', sans-serif;
      font-size: 24px;
      font-weight: 400;
      line-height: normal;
      color: #fff;

      & span {
        display: block;
        font-size: 36px;
        font-weight: 700;
      }

    }

  }

}

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .progress {
    background-size: 200%;

    &__item {

      & p {
        font-size: 18px;

        & span {
          font-size: 28px;
        }

      }

    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .progress {
    background-size: 400%;

    &__item {
      &-col {
        &-4-12 {
          width: 100%;
          //padding: 0 45px 45px 0;
        }
      }
    }

    &__item {

      & p {
        font-size: 16px;
        padding: 10px 25px;

        & span {
          font-size: 24px;
        }

      }

    }

  }

}

/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {

  .progress {
    background-size: 420%;
  }

}

