.plus {
  padding-bottom: 100px;
  background: url(../images/orange_bg.jpg) no-repeat;
  background-position: bottom;
  background-size: 100%;

  &__item-list {
    padding: 0 20px;

    &:after {
      clear: both;
      content: "";
      display: table;
    }

  }

  &__item {
    float: left;
    width: 25%;
    text-align: center;

    & p {
      margin: 0;
      padding-top: 20px;
      font-family: 'Open Sans', sans-serif;
      font-size: 24px;
      font-weight: 400;
      line-height: normal;
      color: #2a2521;
    }

  }

}

.plus-cross {

  &__row {
    display: flex;
    flex-flow: row wrap;
  }

  &__col {

    &-6-12 {
      width: 50%;
    }

  }


  &__item {
    //display: inline-block;
    //height: 100%;
    //width: 100%;

    &_gr {
      border-radius: 3px 0 0 3px;
      background: #ecf0f4;
    }

    &_or {
      border-radius: 0 3px 3px 0;
      background: #fff2e7;
    }

  }

  &__title {
    margin: 0;
    padding: 45px 20px 0 20px;
    text-align: center;
    font-family: 'Open Sans', sans-serif;
    font-size: 30px;
    font-weight: 600;
    line-height: normal;

    &_gr {
      color: #2a2521;
    }

    &_or {
      color: #fd7400;
    }

  }

  &__list {
    margin: 0;
    padding: 45px 20px 45px 20px;
    font-family: 'Open Sans', sans-serif;
    font-size: 18px;
    font-weight: 600;
    list-style: none;

    & li {
      position: relative;
      padding: 0 0 45px 60px;
      line-height: 30px;

      &:last-child {
        padding-bottom: 0;
      }

    }

    &_gr {

      & li {

        &:before {
          content: "";
          background: url(../images/no.png) no-repeat;
          width: 32px;
          height: 32px;
          position: absolute;
          top: 0px;
          left: 10px;
        }

      }
    }

    &_or {

      & li {

        &:before {
          content: "";
          background: url(../images/yes.png) no-repeat;
          width: 32px;
          height: 32px;
          position: absolute;
          top: 0px;
          left: 10px;
        }

      }

    }

  }

}


/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {

  .plus {
    background-size: 300%;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .plus {
    //padding-bottom: 100px;
    //background-position: bottom;
    background-size: 200%;

    &__item {
      width: 25%;

      & img {
        width: 96px;
      }

      & p {
        margin: 0;
        padding-top: 20px;
        font-size: 18px;
        font-weight: 400;
        line-height: normal;
        color: #2a2521;
      }

    }
  }

  .plus-cross {

    &__row {
      //display: flex;
      //flex-flow: row wrap;
    }

    &__col {

      &-6-12 {
        //width: 50%;
      }

    }


    &__item {

      &_gr {
        border-radius: 0;
      }

      &_or {
        border-radius: 0;
      }

    }

    &__title {
      font-size: 22px;
    }

    &__list {
      font-size: 14px;

      & li {
        padding: 0 0 45px 60px;
        line-height: 22px;
      }

    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .plus {
    padding-bottom: 40px;
    //background-position: bottom;
    background-size: 320%;

    &__item {
      width: 50%;
      padding-bottom: 20px;

      & img {
        width: 80px;
      }

      & p {
        font-size: 16px;
        font-weight: 400;
      }

    }
  }

  .plus-cross {

    &__row {
      //display: flex;
      flex-flow: row wrap;
    }

    &__col {

      &-6-12 {
        width: 100%;
      }

    }

    &__title {
      padding: 35px 20px 0 20px;
    }

    &__list {
      font-size: 14px;
      padding: 35px 20px 35px 20px;

      & li {
        padding: 0 0 25px 60px;
      }

    }

  }

}

/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px) {

  .plus {
    background: #ff9738;
  }

}

