.friends-carousel {

  &__item {
    //background: #0000AA;
  }

  &__item-icon {
    width: 100%;
  }

  &__item-text {
    padding: 0 30px;
    font-family: 'Open Sans', sans-serif;

    & h3 {
      margin: 0;
      color: #fd7400;
      text-transform: uppercase;
      font-size: 24px;
      font-weight: 700;
      line-height: normal;
    }

    & p {
      font-size: 16px;
      font-weight: 400;
      line-height: normal;
    }

  }

  &__nav-container {
    text-align: center;
    padding-top: 20px;
  }

  &__nav-prev {
    display: inline-block;
    margin: 0 10px;
    width: 32px;
    height: 32px;
    border-radius: 100%;
    background: url(../images/car-prev.png) no-repeat;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(.25,.8,.25,1);

    &:hover {
      box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    }

    &:active {
      opacity: .8;
    }

  }

  &__nav-next {
    display: inline-block;
    margin: 0 10px;
    width: 32px;
    height: 32px;
    border-radius: 100%;
    background: url(../images/car-next.png) no-repeat;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(.25,.8,.25,1);

    &:hover {
      box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    }

    &:active {
      opacity: .8;
    }

  }

}


