.trainers {
  background: #0f0f0f;
  padding-bottom: 60px;

  &__row {

    &:after {
      clear: both;
      content: "";
      display: table;
    }

  }

  &__col {

    &-4-12 {
      float: left;
      width: 33.33%;
    }

    &-6-12 {
      float: left;
      width: 50%;
    }

    &-8-12 {
      float: left;
      width: 66.66%;
    }

  }


  &__box {
    padding: 0 40px;

    &_bdr {
      border-right: 1px solid rgba(255, 255, 255, .13);
    }

  }

  &__item {
    padding: 20px 0;
  }

  &__icon {
    vertical-align: top;
    width: 100%;
    border-radius: 100%;
  }

  &__text {
    padding: 0 20px;
    line-height: normal;
    color: #E0E0E0;

    & h4 {
      font-family: 'Roboto Slab', serif;
      font-size: 24px;
      font-weight: 300;
      margin: 0;
    }

    & p {
      font-family: 'Open Sans', sans-serif;
      font-size: 16px;
      line-height: 24px;
    }

    & .quote {
      font-family: 'Roboto Slab', serif;
      font-style: italic;
      font-weight: 300;
    }

  }

}


/* Large Devices, Wide Screens */
@media only screen and (max-width : 1200px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .trainers {
    padding-bottom: 0;

    &__col {

      &-4-12 {
        position: relative;
        width: 100%;
      }

      &-6-12 {
        width: 100%;
      }

      &-8-12 {
        width: 100%;
      }

    }

    &__box {
      padding: 0 20px 20px 20px;

      &_bdr {
        border-right: none;
      }

    }

    &__icon {
      position: relative;
      width: 240px;
      left: 50px;
    }

    &__text {
      padding: 40px 0;

      & h4 {
        font-size: 22px;
        text-align: center;
      }

      & p {
        font-size: 15px;
      }

    }


  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .trainers {
    padding-bottom: 0;

    &__col {

      &-4-12 {
        width: 100%;
      }

      &-6-12 {
        width: 100%;
      }

      &-8-12 {
        width: 100%;
      }

    }

    &__box {
      padding: 0 20px 20px 20px;

      &_bdr {
        border-right: none;
      }

    }

    &__icon {
      position: relative;
      width: 100%;
      left: 0;
    }

    &__text {
      padding: 20px 0;

      & h4 {
        font-size: 22px;
        text-align: center;
      }

      & p {
        font-size: 15px;
      }

    }


  }


}

