.footer {
  margin-top: 60px;
  background: url(../images/grey_bg.jpg) no-repeat;
  background-position: top;
  background-size: 100%;


  &:after {
    clear: both;
    content: "";
    display: table;
  }

  &__box {
    position: relative;
    padding: 40px 20px;
    z-index: 1;

    &:after {
      clear: both;
      content: "";
      display: table;
    }

  }

  &__logo {
    float: left;

    & > img {
      width: 170px;
    }

  }

  &__call {
    float: right;
    font-family: 'Open Sans', sans-serif;
    color: #fff;
    text-align: right;
  }

  &__call-phone {
    position: relative;
    display: inline-block;
    font-size: 22px;
    font-weight: 300;
    padding-bottom: 22px;

    &:before {
      content: "";
      background: url(../images/phone-sm.png) no-repeat;
      width: 22px;
      height: 22px;
      position: absolute;
      left: -30px;
    }

  }
}



